import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import WeekDays from './WeekDays'
const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },
  name: {
    displayName: 'RulesName',
  },
  dayOfWeek: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: WeekDays.getList(),
        itemText: 'name',
      },
    },
  },
  startTime: {
    type: IKDataEntity.Types.Time,
  },
  endTime: {
    type: IKDataEntity.Types.Time,
  },
  tempDiscountStr: {
  },
  priority: {
    formConfig: {
      default: 20,
    },
  },
  isActive: {
    type: IKDataEntity.Types.Boolean,
  },
}
const config = {
  add: function (item) {
    if (item.dayOfWeek === 7) {
      item.dayOfWeek = 0
    }
    return hillo.post('DiscountRule.php?op=add', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    if (item.dayOfWeek === 7) {
      item.dayOfWeek = 0
    }
    return hillo.postWithUploadFile('DiscountRule.php?op=change', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('DiscountRule.php?op=delete', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('DiscountRule.php?op=getList', { ...filter }))
      .content.map(function (item) {
        if (item.dayOfWeek === '0') {
          item.dayOfWeek = '7'
        }
        return item
      })
  },
}
export default IKDataEntity.ModelFactory(entity, config)
